body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
  min-height: 100vh;
  background-color: #9BD3D6; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin; }

.App-link {
  color: #61dafb; }

.flex {
  display: flex;
  flex-direction: row; }
  .flex- {
    flex: 1 1; }
    .flex-column {
      flex-direction: column; }
    .flex-1 {
      flex: 1 1; }
    .flex-2 {
      flex: 2 1; }
  .flex-auto {
    flex: 1 0 auto; }

.flex.center {
  justify-content: center; }
  .flex.center-center {
    align-items: center; }
  .flex.center-start {
    align-items: flex-start; }
  .flex.center-end {
    align-items: flex-end; }

.flex.start {
  justify-content: start; }
  .flex.start-center {
    align-items: center; }
  .flex.start-start {
    align-items: flex-start; }
  .flex.start-end {
    align-items: flex-end; }

.flex.end {
  justify-content: end; }
  .flex.end-center {
    align-items: center; }
  .flex.end-start {
    align-items: flex-start; }
  .flex.end-end {
    align-items: flex-end; }

.padding-1 {
  padding: 1em; }

.margin-1 {
  margin: 1em; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.viewStyle {
  background-color: #26374D;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  position: 'relative';
  padding: 0 1em; }

.textStyle {
  font-size: 20px;
  color: #9BD3D6; }

.user-menuButton {
  margin-left: -12px;
  margin-right: 20px; }

.user-photo {
  height: 2em; }

#inputSearch {
  border-color: grey; }

.cardElement {
  margin-bottom: 1em;
  border: 1px solid grey;
  cursor: pointer; }
  .cardElement .cover {
    width: calc(100% / 2.8);
    min-width: calc(100% / 2.8);
    max-width: calc(100% / 2.8); }

.list {
  padding: 0 1em 1em 1em; }

iframe {
  height: calc(100vw * 8 / 16); }

@media screen and (min-width: 820px) {
  #videoDetail {
    display: table; }
    #videoDetail iframe {
      height: calc((100vw * 9 / 16)/2); } }

@media screen and (min-width: 820px) {
  .videoContent {
    display: flex; } }

